#navRightTopLanguages{
    justify-content: flex-end;
}

#navRightMenuOption{
    justify-content: flex-end;
}

.navbar{
    justify-content: flex-end;
    border-bottom-style: solid;
    border-bottom-width: thin;
    border-bottom-color: lightgray;
}


.nav-link:link{
    color: #ffffff;
    text-decoration: none;
}

.nav-link:visited{
    color: #ffffff;
    text-decoration: none;
}

.nav-link:hover{
    text-decoration: underline;
}
